
import Header from '../../Sidebar/Header'
import Menu2 from '../../Sidebar/Menu2'




const Dasboard = () => {

  return (

    <div>
      <div className='header'>
       
      <Header/> 
    
      <Menu2/>
      </div>
      <div className="content-wrapper" >


        {/* Content Header (Page header) */}

        <div className='container' >
         
       
        <div className='container'>
            <img src="data:image/png;base64," alt="logo"  />
          </div>
          </div> 
          <div>
            <h3>Welcome Administrator. </h3><br />

              {/* <p>Your Gateway to be a Champion by Knowing and leveraging you purpose, Passion & potential leading you to the path of maximum effectiveness and fulfilment in your endeavors.</p> */}
         


          <div className="container-fluid">
            <div className="row mb-2">
            
              <div className="col-sm-6">
                <h1 className="m-0 text-dark">Dash</h1>
              </div>
             
              {/* /.col */}
              <div className="col-sm-6">
                <ol className="breadcrumb float-sm-right">
                  <li className="breadcrumb-item"><div>Home</div></li>
                  <li className="breadcrumb-item active">Dashboard v1</li>
                </ol>
              </div>{/* /.col */}
            </div>{/* /.row */}
          </div>{/* /.container-fluid */}
        </div>
        {/* /.content-header */}
        {/* Main content */}
        
        <section className="content">
          <div className="container-fluid">
            {/* Small boxes (Stat box) */}
            <div className="row">
              <div className="col-lg-3 col-6">
                {/* small box */}
                <div className="small-box bg-info">
                  <div className="inner">
                    <h3>150</h3>
                    <p>New Orders</p>
                  </div>
                  <div className="icon">
                    <i className="ion ion-bag" />
                  </div>
                  <div className="small-box-footer">More info <i className="fas fa-arrow-circle-right" /></div>
                </div>
              </div>
              {/* ./col */}
              <div className="col-lg-3 col-6">
                {/* small box */}
                <div className="small-box bg-success">
                  <div className="inner">
                    <h3>53<sup style={{ fontSize: 20 }}>%</sup></h3>
                    <p>Bounce Rate</p>
                  </div>
                  <div className="icon">
                    <i className="ion ion-stats-bars" />
                  </div>
                  <div className="small-box-footer">More info <i className="fas fa-arrow-circle-right" /></div>
                </div>
              </div>
              {/* ./col */}
              <div className="col-lg-3 col-6">
                {/* small box */}
                <div className="small-box bg-warning">
                  <div className="inner">
                    <h3>44</h3>
                    <p>User Registrations</p>
                  </div>
                  <div className="icon">
                    <i className="ion ion-person-add" />
                  </div>
                  <div className="small-box-footer">More info <i className="fas fa-arrow-circle-right" /></div>
                </div>
              </div>
            
              <div className="col-lg-3 col-6">
              
                <div className="small-box bg-danger">
                  <div className="inner">
                    <h3>65</h3>
                    <p>Unique Visitors</p>
                  </div>
                  <div className="icon">
                    <i className="ion ion-pie-graph" />
                  </div>
                  <div className="small-box-footer">More info <i className="fas fa-arrow-circle-right" /></div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>

  )
}

export default Dasboard